<template>
  <div class="content">
    <div class="block-content bg-white-op-90">
      <div class="table-responsive">
        <table class="table table-striped table-vcenter">
          <thead>
            <tr>
              <th>#</th>
              <th>Email</th>
              <th>Amount</th>
              <th>Gateway</th>
              <th>Address</th>
              <th>Status</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(withdrawal, index) in withdrawals" :key="withdrawal.id">
              <td>{{index + 1}}</td>
              <td>{{ withdrawal.user }}</td>
              <td class="text-danger">{{currency}}{{withdrawal.amount}}</td>
              <td>{{withdrawal.currency}}</td>
              <td>{{withdrawal.address}}</td>
              <td>
                <span class="text-success" v-if="withdrawal.status == 1">Approved</span>
                <span class="text-danger" v-else>Pending</span>
              </td>
              <td>
                <button
                  @click.prevent="approve(withdrawal.id)"
                  class="btn bg-primary text-white"
                >Approve</button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- hash modal -->
    <div
      class="modal fade"
      id="modal-popin"
      tabindex="-1"
      role="dialog"
      aria-labelledby="modal-popin"
      style="display: none;"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-popin modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="block block-themed block-transparent mb-0">
            <div class="block-header bg-primary">
              <h3 class="block-title">Add Transaction Hash</h3>
              <div class="block-options">
                <button
                  type="button"
                  class="btn-block-option"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <i class="si si-close"></i>
                </button>
              </div>
            </div>
            <div class="block-content">
              <form @submit.prevent="update(withdrawalID)">
                <div class="col-md-12">
                  <div class="form-group">
                    <div>
                      <label>Hash</label>
                      <input type="text" class="form-control" v-model="hash" />
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="form-material">
                      <div v-if="processing" class="col-6 col-md-3">
                        <i class="fa fa-2x fa-cog fa-spin text-primary"></i>
                      </div>
                      <button v-else type="submit" class="btn btn-outline-primary mb-10">Approve</button>
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { RepositoryFactory as Repo } from "../../../repository/RepositoryFactory";
const AR = Repo.get("academy");
export default {
  data() {
    return {
      processing: false,
      withdrawals: [],
      currency: "",
      withdrawalID: "",
      hash: ""
    };
  },
  methods: {
    update(id) {
      this.processing = true;
      let data = {
        id: id,
        hash: this.hash
      };
      AR.approveWithdrawal(data)
        .then(res => {
          this.processing = false;
          Fire.$emit("Refresh");
          $("#modal-popin").modal("hide");
          toast.fire({
            type: "success",
            title: "Approved!"
          });
        })
        .catch(() => {
          this.processing = false;
          toast.fire({
            type: "error",
            title: "Error! The transaction hash is required"
          });
        });
    },
    approve(id) {
      this.withdrawalID = id;
      $("#modal-popin").modal("show");
    },
    getInfo() {
      AR.getAllWithdrawals().then(response => {
        this.currency = this.$store.state.currency;
        let withdrawals = response.data;
        let updated = [];
        withdrawals.map(withdrawal => {
          if (withdrawal.status == 0) {
            updated.push(withdrawal);
          }
        });
        this.withdrawals = updated;
      });
    }
  },
  created() {
    this.getInfo();
    Fire.$on("Refresh", () => {
      this.getInfo();
    });
  }
};
</script>
