<template>
  <div class="content bg-white-op-90">
    <div class="row">
      <div class="col-lg-4">
        <form action="be_pages_generic_search.html" method="post">
          <div class="input-group">
            <input
              type="text"
              class="form-control text-primary bg-primary-lighter"
              placeholder="Enter email or name"
              id="page-header-search-input"
              v-model="search"
            />
          </div>
        </form>
      </div>
    </div>
    <div class="block-content">
      <div class="table-responsive">
        <table class="table table-striped table-vcenter">
          <thead>
            <tr>
              <th>#</th>
              <th>Name</th>
              <th>Total Earned</th>
              <th>Balance</th>
              <th>Ban Status</th>
              <th>Email Status</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(tutor, index) in filteredTutors" :key="tutor.id">
              <td>{{ index + 1 }}</td>
              <td>{{ tutor.name }}</td>
              <td>${{ tutor.total_earned }}</td>
              <td>${{ tutor.balance }}</td>
              <td>
                <span v-if="tutor.ban == '0'" class="text-success">
                  <b>Active</b>
                </span>
                <span v-else class="text-danger">
                  <b>Banned</b>
                </span>
              </td>
              <td>
                <span v-if="tutor.email_verified_at !== null" class="text-success">
                  <b>Verified</b>
                </span>
                <span v-else class="text-danger">
                  <b>Unverified</b>
                </span>
              </td>
              <td>
                <button
                  type="button"
                  @click.prevent="showTutorModal(tutor)"
                  class="btn btn-sm btn-primary js-tooltip-enabled"
                >
                  <i class="fa fa-eye"></i>
                </button>
                <button
                  type="button"
                  class="btn btn-sm btn-danger js-tooltip-enabled"
                  @click.prevent="deleteUser(tutor.id)"
                >
                  <i class="fa fa-trash"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div
      class="modal fade"
      id="modal-popin"
      tabindex="-1"
      role="dialog"
      aria-labelledby="modal-popin"
      style="display: none;"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg modal-dialog-popin modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="block block-themed block-transparent mb-0">
            <div class="block-header bg-primary">
              <h3 class="block-title">Tutor Information</h3>
              <div class="block-options">
                <button
                  type="button"
                  class="btn-block-option"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <i class="si si-close"></i>
                </button>
              </div>
            </div>

            <div
              class="block-content block-content-full block-content-sm bg-body bg-white-op-90 font-size-sm"
            >
              <div class="col-md-6 col-xl-6">
                <div class="block-content block-content-full clearfix">
                  <div class="float-left">
                    <!-- <img class="img-avatar img-avatar96" :src="'/images/avatar9.jpg'" alt /> -->
                    <img class="img-avatar img-avatar96" :src="'/storage/avatars/'+image" alt />
                  </div>
                </div>
              </div>
              <div class="text-primary">
                <b>Email:</b>
                {{ tutor.email }}
              </div>
              <div class="text-primary py-5">
                <b>Joined:</b>
                {{ tutor.joined }}
              </div>
              <div class="text-primary py-5">
                <b>Verification Status:</b>
                <span v-if="status == 1" class="text-warning">PENDING</span>
                <span v-else class="text-success">VERIFIED</span>
              </div>
              <div class="text-primary mb-10">
                <b>Ban Status:</b>
                <span v-if="tutor.ban != '0'" class="text-danger">BANNED</span>
                <span v-else class="text-success">NOT BANNED</span>
              </div>
              <div>
                <a
                  class="text-danger font-w700"
                  @click.prevent="showId"
                  href="#"
                >Click to view tutor identification card</a>
              </div>

              <div class="mt-10">
                <div class="row">
                  <div class="ml-15 mr-10">
                    <button
                      v-if="tutor.ban == '0'"
                      @click.prevent="update(tutor.id)"
                      class="btn btn-sm btn-danger"
                    >Ban Account</button>
                    <button
                      @click.prevent="update(tutor.id)"
                      v-else
                      class="btn btn-sm btn-success"
                    >Activate Account</button>
                  </div>
                  <div>
                    <button
                      v-if="status == 1"
                      @click.prevent="verify(tID)"
                      class="btn btn-sm bg-primary text-white"
                    >Verify Account</button>
                    <button
                      v-else
                      @click.prevent="verify(tID)"
                      class="btn btn-sm bg-primary text-white"
                    >Unverify Account</button>
                  </div>
                </div>
              </div>
            </div>

            <div class="block-content bg-white-op-90 block-content-full bg-body">
              <div class="col-12">
                <h6>Tutor Courses</h6>
              </div>
              <div class="table-responsive">
                <table class="table table-striped table-vcenter">
                  <tbody>
                    <tr v-for="(course, index) in tutor.courses" :key="course.id">
                      <td>{{ index + 1 }}</td>
                      <td>{{ course.title }}</td>
                      <td>${{ course.price }}</td>
                      <td>{{ course.total_chapters }} Chapters</td>
                      <td>{{ course.total_outlines }} Lessons</td>
                      <td>
                        <span v-if="course.status == '1'" class="text-success">
                          <b>Approved</b>
                        </span>
                        <span v-else class="text-warning">
                          <b>Pending</b>
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- licence modal -->
    <div class="modal fade" id="modal-small" tabindex="-1">
      <div class="modal-dialog modal-dialog-sm modal-dialog-centered">
        <div class="modal-content">
          <div class="block-header bg-primary">
            <h3 class="block-title text-white">Tutor Identification</h3>
            <div class="block-options">
              <button
                type="button"
                class="btn-block-option text-white"
                data-dismiss="modal"
                aria-label="Close"
              >
                <i class="si si-close"></i>
              </button>
            </div>
          </div>
          <a href="#" class="modal-close" data-dismiss="modal" aria-label="Close">
            <em class="ti ti-close"></em>
          </a>
          <div class="popup-body">
            <div class="container">
              <div class="modal-body mb-0 p-0 mt-3">
                <img style="width: 100% !important" :src="'/storage/identity/' + id_card" alt="..." />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { RepositoryFactory as Repo } from "../../../repository/RepositoryFactory";
const AR = Repo.get("academy");
export default {
  data() {
    return {
      tutors: [],
      tutor: {},
      search: "",
      image: "images/avatar9.jpg",
      id_card: "",
      status: "",
      tID: ""
    };
  },
  methods: {
    verify(id) {
      AR.verifyTutor(id).then(() => {
        Fire.$emit("Refresh");
        $("#modal-popin").modal("hide");
        toast.fire({
          type: "success",
          title: "Verified"
        });
      });
    },
    deleteUser(id) {
      swal
        .fire({
          title:
            "Are you sure?. Courses Associated with this tutor will be lost forever",
          text: "You won't be able to revert this!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yes, delete it!"
        })
        .then(result => {
          // Send request to the server
          if (result.value) {
            AR.adminDeleteUser(id)
              .then(response => {
                Fire.$emit("Refresh");
                toast.fire({
                  type: "success",
                  title: "Tutor Deleted"
                });
              })
              .catch(error => {
                if (error.response.status == 403) {
                  toast.fire({
                    type: "error",
                    title: "Action Unauthorized"
                  });
                }
                swal("Failed!", "There was something wrong.", "warning");
              });
          }
        });
    },
    update(id) {
      AR.adminUpdateUser(id).then(() => {
        Fire.$emit("Refresh");
        $("#modal-popin").modal("hide");
        toast.fire({
          type: "success",
          title: "Updated"
        });
      });
    },
    showTutorModal(tutor) {
      this.image = tutor.tutor_info.image;
      this.id_card = tutor.tutor_info.identity;
      this.status = tutor.tutor_info.status;
      this.tID = tutor.tutor_info.id;
      //   console.log(this.status);
      this.tutor = tutor;
      $("#modal-popin").modal("show");
    },
    showId() {
      $("#modal-small").modal("show");
    },
    getTutors() {
      AR.getTutors().then(res => {
        let tutors = res.data.tutors;
        let updated = [];
        tutors.map(tutor => {
          if (tutor.tutor_info.status == 2) {
            updated.push(tutor);
          }
        });
        this.tutors = updated;
      });
    }
  },
  created() {
    this.getTutors();
    Fire.$on("Refresh", () => {
      this.getTutors();
    });
  },
  computed: {
    filteredTutors() {
      return this.tutors.filter(tutor => {
        return (
          tutor.name.toLowerCase().includes(this.search.toLowerCase()) ||
          tutor.email.toLowerCase().includes(this.search.toLowerCase())
        );
      });
    }
  }
};
</script>
