<template>
  <div class="py-20 bg-white-op-90">
    <div class="block-content">
      <button type="button" class="btn bg-primary text-white mr-5 mb-5" @click.prevent="newModal">
        <i class="fa fa-plus mr-5"></i> Plan
      </button>
    </div>

    <div class="block-content">
      <div class="table-responsive">
        <table class="table table-striped table-vcenter">
          <thead>
            <tr>
              <th>Name</th>
              <th>Duration (Monthly)</th>
              <th>Amount</th>
              <th class="text-center" style="width: 100px;">Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="plan in plans" :key="plan.id">
              <td class="font-w600">{{plan.name}}</td>
              <td>{{plan.duration}}</td>
              <td>{{plan.amount}}</td>
              <td class="text-center">
                <div class="btn-group">
                  <button
                    type="button"
                    class="btn btn-sm btn-primary js-tooltip-enabled"
                    data-toggle="tooltip"
                    title
                    data-original-title="Edit"
                    @click="editModal(plan)"
                  >
                    <i class="fa fa-pencil"></i>
                  </button>
                  <button
                    type="button"
                    @click="planDelete(plan.id)"
                    class="btn btn-sm btn-danger js-tooltip-enabled"
                  >
                    <i class="fa fa-trash"></i>
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div
      class="modal fade"
      id="modal-fadein"
      tabindex="-1"
      role="dialog"
      aria-labelledby="modal-fadein"
      style="display: none;"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h3 v-show="editmode" class="card-title">Update Plan</h3>
            <h3 v-show="!editmode" class="card-title">Add New Plan</h3>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <!-- Beginning of the form -->
          <form @submit.prevent="editmode ? planUpdate() : newPlan()">
            <div class="modal-body">
              <div class="card">
                <div class="card-body">
                  <div class="form-group">
                    <label for="name">Plan Name</label>
                    <input v-model="form.name" type="text" class="form-control" id="name" required />
                  </div>
                  <div class="form-group">
                    <label for="max">Amount</label>
                    <input type="text" class="form-control" id="max" required v-model="form.amount" />
                  </div>

                  <div class="form-group">
                    <label for="duration">Duration (monthly)</label>
                    <input
                      type="text"
                      class="form-control"
                      id="duration"
                      required
                      v-model="form.duration"
                    />
                  </div>
                  <button
                    type="submit"
                    v-show="editmode"
                    class="btn btn-lg bg-primary text-white mb-2"
                  >Update</button>
                  <button
                    type="submit"
                    v-show="!editmode"
                    class="btn btn-lg bg-primary text-white mb-2"
                  >Create</button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { RepositoryFactory } from "../repository/RepositoryFactory";
import axios from "axios";

const AR = RepositoryFactory.get("academy");
export default {
  data() {
    return {
      editmode: false,
      plans: [],
      styles: [],
      form: new Form({
        id: "",
        name: "",
        amount: "",
        duration: ""
      })
    };
  },
  methods: {
    resetData() {
      this.form = [];
    },
    newModal() {
      this.editmode = false;
      this.form.reset();
      $("#modal-fadein").modal("show");
    },
    editModal(plan) {
      this.editmode = true;
      this.form.reset();
      $("#modal-fadein").modal("show");
      this.form.fill(plan);
    },
    planDelete(id) {
      swal
        .fire({
          title: "Are you sure?",
          text: "You won't be able to revert this!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yes, delete it!"
        })
        .then(result => {
          // Send request to the server
          if (result.value) {
            AR.deletePlan(id)
              .then(response => {
                // const projectIndex = this.plans.findIndex(
                //   p => p.id === plan.id
                // );
                // this.plans.splice(projectIndex, 1);
                Fire.$emit("Refresh");
                swal.fire(
                  "Deleted!",
                  response.data.message,
                  response.data.status
                );
              })
              .catch(error => {
                if (error.response.status == 403) {
                  toast.fire({
                    type: "error",
                    title: "Action Unauthorized"
                  });
                }
                swal("Failed!", "There was something wrong.", "warning");
              });
          }
        });
    },
    planUpdate() {
      let data = {
        name: this.form.name,
        amount: this.form.amount,
        duration: this.form.duration,
        id: this.form.id
      };
      AR.updatePlan(data)
        .then(response => {
          this.plans = response.data;
          Fire.$emit("Refresh");
          $("#modal-fadein").modal("hide");
          toast.fire({
            type: "success",
            title: "Plan updated successfully"
          });
        })
        .catch(error => {
          if (error.response.status == 403) {
            toast.fire({
              type: "error",
              title: "Action Unauthorized"
            });
          }
        });
    },
    newPlan() {
      this.$Progress.start();
      let data = {
        name: this.form.name,
        amount: this.form.amount,
        duration: this.form.duration
      };
      AR.createPlan(data)
        .then(response => {
          this.plans = response.data;
          $("#modal-fadein").modal("hide");
          toast.fire({
            type: "success",
            title: "Plan created successfully"
          });
          this.$Progress.finish();
          Fire.$emit("Refresh");
        })
        .catch(error => {
          if (error.response.status == 403) {
            toast.fire({
              type: "error",
              title: "Action Unauthorized"
            });
          }
        });
    },
    loadPlans() {
      AR.viewPlans().then(response => {
        this.plans = response.data;
      });
    }
  },
  created() {
    this.loadPlans();
    Fire.$on("Refresh", () => {
      this.loadPlans();
    });
  }
};
</script>
